@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);

*{
    margin-top: 0;
    margin-bottom: 0;
}

body{
    --twitter-color: #50b7f5;
    --twitter-background: #e8f5fe;
    font-family: 'Ubuntu', sans-serif;
}

.app{
    display: flex;
    height: 100vh;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
}
.sidebar{
    border-right: 1px solid var(--twitter-background);
    flex: 0.3 1;
    /* min-width: 250px; */
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

.sidebar_twitterIcon{
    color: var(--twitter-color);
    font-size: 30px !important;
    margin-left: 20px;
    margin-bottom: 20px;
}

.sidebar_tweet{
    background-color: var(--twitter-color) !important;
    color: white !important;
    border: none !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    height: 50px !important;
    margin-top: 20px !important;
    width: 200px !important;
}
.sidebarOption{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarOption:hover{
    background-color: var(--twitter-background);
    border-radius: 30px;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}

.sidebarOption > .MuiSvgIcon-root{
    padding: 20px;
}

.sidebarOption > h2{
    font-weight:  800;
    font-size: 20px;
    margin-right: 20px;
}

.sidebarOption--active{
    color: var(--twitter-color);
}
.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;
}

.post_body{
    flex: 1 1;
    padding: 10px;
}

.post_body > img{
    border-radius: 20px;
}

.post_footer{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post_badge{
    font-size: 14px !important;
    color: var(--twitter-color);
}

.post_headerSpecial{
    font-weight: 600;
    font-size: 12px;
    color: gray;
    text-align: center;
}

.post_headerDescription{
    font-size: 15px;
    margin-bottom: 5px;
}

.post_avatar{
    padding: 20px;
}
.feed{
    flex: 0.4 1;
    border-right: 1px solid var(--twitter-background);
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    max-width: 400px;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.feed::-webkit-scrollbar{
    display: none;
}

.feed_header{
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}

.feed_header > h2{
    font-size: 20px;
    font-weight: 800;
}
.tweetBox{
    padding-bottom: 10px;
    border-bottom: 5px solid var(--twitter-background);
    padding-right: 10px;
    display: flex;
    flex-direction: column;
}

.tweetBox > form{
    display: flex;
    flex-direction: column;
}

.tweetBox_input{
    padding: 20px;
    display: flex;
}

.tweetBox_input > input{
    flex: 1 1;
    margin-left: 20px;
    font-size: 20px;
    border: none;
}

.tweetBox_tweetBtn{
    background-color: var(--twitter-color) !important;
    color: white !important;
    border: none !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 20px !important;
    height: 40px !important;
    margin-top: 20px !important;
    margin-left: auto !important;
}

.tweetBox_imageInput{
    border: none;
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.widgets{
    flex: 0.3 1;
}

.widgets_searchDiv{
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}

.widgets_searchDiv > input{
    border: none;
    background-color: var(--twitter-background);
}

.widgets_searchIcon{
    color: gray;
}

.widgets_container{
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background-color: #f5f8fa;
    border-radius: 20px;
}

.widgets_container > h2{
    font-size: 18px;
    font-weight: 800;
}
